.main-checkout {
    width: 100%;
    margin-bottom: 2rem;
    border-radius: 10px;
    box-shadow: 1px 0 1rem #010;
    background: var(--secondColor);
    padding: 2rem;
    display: flex;
    justify-content: center;
    align-items: center;
    min-height: 100vh;
    .form-contacto {
        padding: 1rem;
        width: 30rem;
        min-height: 30rem;
        display: flex;
        flex-direction: column;
        justify-content: space-evenly;
        align-items: center;
        background: var(--secondColor);
        border-radius: 10px;
        @media (max-width: 480px) {
            padding: 0;
            box-shadow: unset;
            border-radius: unset;
            background: unset;
        }
        .inputForm {
            width: 20rem;
            border-radius: 10px;
            background: var(--secondColor);
            padding: .8rem;
            height: 2rem;
            border: none;
            font-size: 1rem;
            @media (max-width: 480px) {
                width: 100%;
            }
        }
        .inputForm:hover::placeholder {
            color: deeppink;
            transition: all .4s;
        }
        .inputForm:focus {
            outline: none;
        }
    }
}

.checkOut {
    width: 100%;
    border-radius: 10px;
    box-shadow: 1px 0 1rem #010;
    background: var(--secondColor);
    margin-bottom: 2rem;
    padding: 2rem;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    .checkOutDiv {
        min-height: 10rem;
        margin: 3rem;
        display: flex;
        flex-direction: column;
        justify-content: space-evenly;
        align-items: center;
        @media (max-width: 480px) {
            min-height: 15rem;
        }
        .gracias {
            font-size: 2rem;
            @media (max-width: 480px) {
                text-align: center;
            }
        }
        .orden {
            @media (max-width: 480px) {
                text-align: center;
            }
            .ordenNum {
                color: deeppink;
                margin: 0 .5rem;
                letter-spacing: .05rem;
                @media (max-width: 480px) {
                    text-align: center;
                }
            }
        }
    }
}